











import { defineComponent, onMounted, ref } from '@vue/composition-api';

export default defineComponent({
  props: {
    targetSelector: {
      type: String,
      required: true,
    },
    rows: {
      default: 1,
      type: Number,
    },
  },
  // @ts-ignore
  setup(props, { refs }) {
    const height = ref<null | number>(null);
    onMounted(() => {
      const el = (refs.container as HTMLDivElement)!.querySelector(props.targetSelector) as HTMLElement;
      if (el) {
        // console.log(el, el.clientHeight, el.scrollHeight);
        height.value = el.clientHeight * props.rows;
      }
    });
    return {
      height,
    };
  },
});
